.main-header {
  &__library {
    background-color: #10181d;
    margin: 0 auto;
    background-position: center;
    background-size: cover;
    @media screen {
      height: 230px;
      background-image: url('../images/header-library320@1x.png');
      @media (min-device-pixel-ratio: 2), (min-resolution: 2dppx), (min-resolution: 192dpi) {
        background-image: url('../images/header-library320@2x.png');
      }
    }
    @media screen and (min-width: 768px) {
      height: 216px;
      background-image: url('../images/header-library768@1x.png');
      @media (min-device-pixel-ratio: 2), (min-resolution: 2dppx), (min-resolution: 192dpi) {
        background-image: url('../images/header-main768@2x.png');
      }
    }
    @media screen and (min-width: 1280px) {
      background-image: url('../images/header-library1280@1x.png');
      @media (min-device-pixel-ratio: 2), (min-resolution: 2dppx), (min-resolution: 192dpi) {
        background-image: url('../images/header-library1280@2x.png');
      }
    }
  }

  &__container {
    max-width: 320px;
    padding: 44px 20px 0 20px;
    @media (min-width: 321px) {
      max-width: 768px;
      padding: 40px 32px 0 32px;
    }
    @media (min-width: 789px) {
      max-width: 1280px;
    }
  }

  &__menu {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-bottom: 54px;
    @media (min-width: 768px) {
      padding-bottom: 40px;
    }
    @media (min-width: 1280px) {
      padding-bottom: 41px;
    }
  }

  &__link {
    text-decoration: none;
  }

  &__logo {
    @media (min-width: 768px) {
      padding-right: 8px;
    }
    @media (min-width: 1280) {
      padding-right: 10px;
    }
  }

  &__title {
    color: get-color(brand_color-first);
    text-align: center;
    font-size: 30px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    letter-spacing: 0.01em;
    @media (max-width: 767px) {
      display: none;
    }
  }

  &__nav {
    display: flex;
    gap: 40px;
    justify-content: flex-end;
  }

  &__nav-link:link,
  &__nav-link:visited {
    color: get-color(brand_color-first);
    text-decoration: none;
    font-size: get-font-size(primary);
    font-weight: 500;
    text-transform: uppercase;
    letter-spacing: 0.02em;
    line-height: normal;
  }

  &__nav-link:hover,
  &__nav-link:focus {
    position: relative;
    color: get-color(brand_color-first);

    &::after {
      content: '';
      width: 100%;
      position: absolute;
      display: block;
      top: 15px;
      width: 100%;
      height: 3px;
      background-color: get-color(brand_color-fourth);
    }
  }

  &__nav-link--active {
    position: relative;
    text-decoration: none;
    text-transform: uppercase;

    &::after {
      content: '';
      width: 100%;
      position: absolute;
      display: block;
      top: 20px;
      width: 100%;
      height: 3px;
      background-color: get-color(brand_color-fourth);
    }
  }

  &__button {
    display: flex;
    justify-content: center;
    gap: 20px;
    @media (min-width: 768px) {
      gap: 32px;
    }
    @media (min-width: 1280px) {
      padding-bottom: 16px;
    }
  }
}
